<template>
  <div>
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="操作账号：">
            <el-input style="width: 203px" v-model="searchList.loginName" clearable placeholder="操作账号" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="所属角色：">
            <el-select v-model="searchList.roleId" clearable placeholder="请选择所属角色">
              <el-option v-for="(item,index) in roleList" :key="index" :label="item.roleName" :value="item.id"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container" style="margin-top:10px" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <el-table :data="list" border v-loading="formLoading" style="width: 100%">
          <el-table-column prop="loginName" label="登录账号"></el-table-column>
          <el-table-column prop="userName" label="姓名"></el-table-column>
          <el-table-column prop="roleName" label="所属角色"></el-table-column>
          <el-table-column prop="ip" label="IP地址"></el-table-column>
          <el-table-column prop="createTime" label="登录时间"></el-table-column>
        </el-table>
        <div class="flex-row-end" style="padding-top:20px;">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page-sizes="[5,10,15,20]"
            :page-size="searchList.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="listTotal">
          </el-pagination>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { findRoleByShopId } from '@/api/systemSetting/userManage'
import { findByCondition } from '@/api/systemSetting/daily'
export default {
  data(){
    return{
      formLoading:false,
      roleList:[],
      searchList:{
        type:2,
        pageNum:1,
        pageSize:5,
      },
      list:[],
      listTotal:0,
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getList()
    this.getData()
  },
  methods:{
    getData(){
      this.formLoading = true;
      findByCondition(this.searchList).then(res=>{
        console.log('登录日志数据列表',res)
        if(res.code == 200){
          this.list = res.data.list;
          this.listTotal = res.data.total
        }else{
          this.$message.error(res.message);
        }
        this.formLoading = false;
      })
    },
    reset(){
      this.searchList.loginName = ''
      this.searchList.description = ''
      this.searchList.roleId = ''
      this.getData()
    },
    getList(){
      findRoleByShopId({shopId:this.shop.shopId}).then(res=>{
        console.log('所有角色',res)
        if(res.code == 200){
          this.roleList = res.data
        }
      })
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .checkBoxDiv{
    font-size: 14px;
    margin-left: 40px;
    line-height: 19px;
    color: #606266;
  }
</style>